
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
directives: {
TransferDom
},
components: {
Tab,
TabItem,
Sticky,
ViewBox,
XHeader,
Panel,
Flexbox,
FlexboxItem,
Previewer,
TransferDom,
Popup,
Loading
},
data() {
return {
model: 1,
show: false,
showSpace: false,

open_forward: false,
recommendList: [],
followList: [{
user_name: '关注君',
publish_time: '2017-03-04',
content: 'lorem dndksdjsknskfnkdfndkfnke',

thumbs_amount: 15
}, {
user_name: '啦啦啦',
publish_time: '2017-05-06',
content: '小女生了看电视了肯德基是来看待聚少离多即使恐龙',

thumbs_amount: 70
}, {
user_name: '好好好',
publish_time: '2017-05-06',
content: 'lsdskldjslkdjsldjsldkjslkdjslkdjsldjslkd',

thumbs_amount: 7
}]
}
},
mounted() {
this.update();
},
methods: {
setModel: function(type) {
this.model = type;
},
update() {
this.show = true;

this.$post(
"dynamic/getList", {},
res => {
console.log("获取过来的动态列表", res);
this.recommendList = res.msg;
this.show = false;
if(res.res >= 1) {}
if(res < 0) {}
}
);
},
spaceChange() {
this.showSpace = !this.showSpace
this.$nextTick(() => {
this.$refs.sticky.bindSticky()
})
},

toMessage() {
this.$router.push({
path: "/message/message",
});
},

thumbs(list) {
var dynamic_id = list.dynamic_id;
console.log("点赞的id", dynamic_id);

this.$post(
"dynamic/up", {
dynamic_id: dynamic_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.is_thumbs_up == false;
this.$vux.toast.show({
text: "点赞成功",
type: "text",
position: "middle"
});
}
if(res.res < 0) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}
}
);

},
cancel_thumbs(list) {
var dynamic_id = list.dynamic_id;
console.log("取消点赞的id", dynamic_id);
this.$post(
"dynamic/delUp", {
dynamic_id: dynamic_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.is_thumbs_up == true;
this.$vux.toast.show({
text: "已取消点赞",
type: "text",
position: "middle"
});
}
if(res.res < 0) {
this.$vux.toast.show({
text: "网络错误",
type: "text",
position: "middle"
});
}
}
);
},


toSocialInfo(group) {
var dynamic_id = group.dynamic_id;
this.$router.push({
path: "/socialInfo/socialInfo",
name: '/socialInfo/socialInfo',
params: {
openComment: "2",
dynamic_id: dynamic_id
}
});
},

toComment() {
this.$router.push({
path: "/socialInfo/socialInfo",
name: '/socialInfo/socialInfo',
params: {
openComment: "1",
}
});
},

toGoodsInfo(item) {
var goods_id = item.goods_id
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: '/goodsInfo/goodsInfo',
params: {
goods_id: goods_id
}
});
},

openForward() {
this.open_forward = true;
},
wx() {
console.log("点击微信转发");
},
qq() {
console.log("点击QQ转发");
},
friend() {
console.log("点击朋友圈转发");
},
copy() {
console.log("点击复制链接");
},

cancel() {
this.open_forward = false;
},

toPersonal() {
this.$router.push({
path: "/personal/personal",
name: '/personal/personal',



});
},

toRelease() {
this.$router.push({
path: "/release/release",
});
}
},
}



